<template>
	<div class="project_list">
		<el-card class="box-card">
			<!--  -->
			<div slot="header" class="clearfix">
				<el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
				</el-breadcrumb>
			</div>
			<el-card class="box-card">
				<!--  -->
				<el-button type="primary" icon='el-icon-plus' @click='dialogCreate="true"'></el-button>
				<!--  -->
				<el-table :data="projectList" style="width: 100%;margin-bottom: 10px;">
					<el-table-column prop="id" label="ID" width="80" sortable>
					</el-table-column>
					<el-table-column prop="name" label="" width="200">
					</el-table-column>
					<el-table-column prop="desc" label="" width="280">
					</el-table-column>
					<el-table-column prop="leader" label="" width="100" sortable>
					</el-table-column>
					<el-table-column prop="tester" label="" width="100">
					</el-table-column>
					<el-table-column prop="interfaces" label="" width="120" sortable>
					</el-table-column>
					<el-table-column prop="testcases" label="" width="80">
					</el-table-column>
					<el-table-column prop="testsuits" label="" width="80">
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<el-button size="mini" @click="proEdit(scope.row)"></el-button>
							<el-button size="mini" type="danger" @click="proDelete(scope.row.id)">
							</el-button>
						</template>
					</el-table-column>
				</el-table>
				<!--  -->
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
					:current-page="page" :page-sizes="[5, 10, 20, 30]" :page-size="size"
					layout="total, sizes, prev, pager, next, jumper" :total="count">
				</el-pagination>
			</el-card>
		</el-card>

		<!--  -->
		<el-dialog title="" :visible.sync="dialogEdit">
			<el-form :model="editProject" :rules="caseRules" ref='updateRef' label-width="80px">
				<el-form-item label="" prop="name">
					<el-input v-model="editProject.name" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="leader">
					<el-input v-model="editProject.leader" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tester">
					<el-input v-model="editProject.tester" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="programmer">
					<el-input v-model="editProject.programmer" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="publish_app">
					<el-input v-model="editProject.publish_app" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editProject.desc" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogEdit = false"> </el-button>
				<el-button type="primary" @click="updateProject"> </el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="dialogCreate" :rules="caseRules" ref='createRef'>
			<template slot='title'>
				<div style="text-align: center;width: 100%;font-size: 24px;"></div>
			</template>
			<el-form :model="newProject" label-width="80px" size='mini' :rules="caseRules" ref='createRef'>
				<el-form-item label="" prop="name">
					<el-input v-model="newProject.name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tester">
					<el-input v-model="newProject.tester" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="leader">
					<el-input v-model="newProject.leader" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="programmer">
					<el-input v-model="newProject.programmer" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="publish_app">
					<el-input v-model="newProject.publish_app" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" :rows="5" v-model="newProject.desc"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogCreate = false"> </el-button>
				<el-button type="primary" @click="createProject"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				projectList: [],
				// 
				page: 1,
				// 
				count: 0,
				//
				size: 10,
				//
				dialogEdit: false,
				editProject: {},
				//
				dialogCreate: false,
				newProject: {
					name: '',
					leader: '',
					tester: '',
					programmer: '',
					publish_app: '',
					desc: ''
				},
				// 
				caseRules: {
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					leader: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					tester: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					programmer: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					publish_app: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ]
				}

			}
		},
		methods: {
			// 
			createProject() {
				// 
				this.$refs.createRef.validate(async (valid) => {
					if (!valid) return
					// 
					const response = await this.$request.post('/projects/', this.newProject)
					if (response.status === 201) {
						this.$message({
							type: 'success',
							message: '!',
							duration: 1000
						});
						// 
						this.getProject()
						// 
						this.dialogCreate = false
					} else {
						console.log(response)
						this.$message.error('!')
					}

				})
			},
			// 
			proEdit(value) {
				console.log(value)
				// 
				this.editProject = {
					...value
				}
				// 
				this.dialogEdit = true
			},
			// 
			async updateProject() {
				// 
				this.$refs.updateRef.validate(async (valid) => {
					if (!valid) return
					const response = await this.$request.put('/projects/' + this.editProject.id + '/', this
						.editProject)
					if (response.status === 200) {
						this.$message({
							message: '',
							type: 'success',
							duration: 1000
						})
						this.getProject()
						this.dialogEdit = false
					} else {
						this.$message({
							message: '',
							type: 'error',
							duration: 1000
						})
					}
				})
			},


			// 
			async proDelete(id) {
				console.log('id', id)
				// 
				const response = await this.$request.delete('/projects/' + id + '/')
				//  resetfuldelete204
				if (response.status === 204) {
					// 
					this.$message({
						message: '',
						type: 'success',
						duration: 1000
					});
					// 
					this.getProject()
				} else {
					this.$message({
						message: '',
						type: 'error',
						duration: 1000
					});
				}
			},
			// 
			async getProject() {
				// 
				const response = await this.$request.get('/projects/', {
					params: {
						page: this.page,
						size: this.size
					}
				})
				if (response.status !== 200) return this.$message.error('')
				// 
				this.projectList = response.data.results
				// 
				this.count = response.data.count
				console.log(response)
			},
			// 
			handleSizeChange(size) {
				this.size = size
				this.page = 1
				this.getProject()
			},
			// 
			handleCurrentChange(page) {
				this.page = page
				this.getProject()
			}
		},

		// vue
		mounted() {
			this.getProject()
		}

	}
</script>

<style>
</style>
