<template>
	<div class="grassland">
		<el-card class="box-card">
			<!--  -->
			<div slot="header" class="clearfix">
				<el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
				</el-breadcrumb>
			</div>
			
			<el-card class="box-card">
				<div slot="header" class="clearfix">
					<!--  -->
					<el-button type="primary" icon='el-icon-plus' @click='dialogCreate="true"'></el-button>
					<!--  -->
					<el-button type="primary" icon='el-icon-delete' ></el-button>
					<!--  -->
					<el-button type="primary" icon='el-icon-refresh' ></el-button>
					<!--  -->
					<el-button type="primary" icon='el-icon-folder-opened' ></el-button>
					<!--  -->
					<el-button type="primary" icon='el-icon-upload' ></el-button>
					<!--  -->
					<el-button type="primary" icon='el-icon-printer' ></el-button>
				</div>
				<!--  -->
				<el-table :data="geoinfoList" style="width: 100%;margin-bottom: 10px;">
					<el-table-column type="selection" width="55"></el-table-column>
					<el-table-column prop="id" label="ID" width="80" sortable>
					</el-table-column>
					<el-table-column prop="vegtype" label="" width="100">
					</el-table-column>
					<el-table-column prop="coverage" label="" width="120">
					</el-table-column>
					<el-table-column prop="growthstatus" label="" width="140">
					</el-table-column>
					<el-table-column prop="lifecycle" label="" width="120">
					</el-table-column>
					<el-table-column prop="biomass" label="" width="140">
					</el-table-column>
					<el-table-column prop="rootdepth" label="" width="140">
					</el-table-column>
					<el-table-column prop="desc" label="" width="300">
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<el-button size="mini" @click="proEdit(scope.row)"></el-button>
							<el-button size="mini"  @click="proView(scope.row)"></el-button>
							<el-button size="mini" type="danger" @click="proDelete(scope.row.id)"></el-button>
						</template>
					</el-table-column>
				</el-table>
				<!--  -->
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
					:current-page="page" :page-sizes="[5, 10, 20, 30]" :page-size="size"
					layout="total, sizes, prev, pager, next, jumper" :total="count">
				</el-pagination>
			</el-card>
		</el-card>

		<!--  -->
		<el-dialog title="" :visible.sync="dialogEdit">
			<el-form :model="editProject" :rules="caseRules" ref='updateRef' label-width="80px">
				<el-form-item label="" prop="vegtype">
					<el-input v-model="editProject.vegtype" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="coverage">
					<el-input v-model="editProject.coverage" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="growthstatus">
					<el-input v-model="editProject.growthstatus" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="lifecycle">
					<el-input v-model="editProject.lifecycle" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="biomass">
					<el-input v-model="editProject.biomass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="rootdepth">
					<el-input v-model="editProject.rootdepth" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" :rows="5" v-model="editProject.desc" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogEdit = false"> </el-button>
				<el-button type="primary" @click="updateProject"> </el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="dialogCreate" :rules="caseRules" ref='createRef'>
			<template slot='title'>
				<div style="text-align: center;width: 100%;font-size: 24px;"></div>
			</template>
			<el-form :model="newProject" label-width="80px" size='mini' :rules="caseRules" ref='createRef'>
				<el-form-item label="" prop="vegtype">
					<el-input v-model="newProject.vegtype" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="coverage">
					<el-input v-model="newProject.coverage" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="growthstatus">
					<el-input v-model="newProject.growthstatus" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="lifecycle">
					<el-input v-model="newProject.lifecycle" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="biomass">
					<el-input v-model="newProject.biomass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="rootdepth">
					<el-input v-model="newProject.rootdepth" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" :rows="5" v-model="newProject.desc" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogCreate = false"> </el-button>
				<el-button type="primary" @click="createProject=false"> </el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="dialogView">
			<template slot='title'>
				<div style="text-align: center;width: 100%;font-size: 24px;"></div>
			</template>
			<el-form :model="viewProject" label-width="80px" size='mini'>		
				<el-form-item label="" prop="vegtype">
					<el-input v-model="viewProject.vegtype" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="coverage">
					<el-input v-model="viewProject.coverage" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="growthstatus">
					<el-input v-model="viewProject.growthstatus" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="lifecycle">
					<el-input v-model="viewProject.lifecycle" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="biomass">
					<el-input v-model="viewProject.biomass" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="rootdepth">
					<el-input v-model="viewProject.rootdepth" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" :rows="5" v-model="viewProject.desc" autocomplete="off" :readonly="true"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogView = false"></el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				geoinfoList: [],				
				// 
				page: 1,
				// 
				count: 0,
				//
				size: 10,
				//
				dialogEdit: false,
				editProject: {},
				//
				dialogView: false,
				viewProject: {},
				//
				dialogCreate: false,
				newProject: {
					vegtype: '',
					coverage: '',
					growthstatus: '',
					lifecycle: '',
					biomass: '',
					rootdepth: '',
					desc: ''
				},
				// 
				caseRules: {
					vegtype: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					coverage: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					growthstatus: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					lifecycle: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					biomass: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					rootdepth: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ]
				}

			}
		},
		created() {
			this.createRandomArrayDict();
		},
		methods: {	
			createRandomArrayDict() {
				const min = 5; // 
				const max = 20; // 
				const length = Math.floor(Math.random() * (max - min + 1)) + min;
				let arrayDict = [];
				for (let i = 0; i < length; i++) {
					let obj_dic = {'id': `${i}`,	
					vegtype: `${i}`,
					coverage: `${i}`,
					growthstatus: `${i}`,
					lifecycle: `${i}`,
					biomass: `${i}`,
					rootdepth: `${i}`,
					desc: `${i}`
				}
					arrayDict.push(obj_dic);
				}				
      			this.geoinfoList = arrayDict;;
			},

			// 
			createProject() {
				// 
				this.$refs.createRef.validate(async (valid) => {
					if (!valid) return
					// 
					const response = await this.$request.post('/projects/', this.newProject)
					if (response.status === 201) {
						this.$message({
							type: 'success',
							message: '!',
							duration: 1000
						});
						// 
						this.getProject()
						// 
						this.dialogCreate = false
					} else {
						console.log(response)
						this.$message.error('!')
					}

				})
			},
			// 
			proEdit(value) {
				console.log(value)
				// 
				this.editProject = {
					...value
				}
				// 
				this.dialogEdit = true
			},
			// 
			proView(value) {
				console.log(value)
				// 
				this.viewProject = {
					...value
				}
				// 
				this.dialogView = true
			},
			// 
			async updateProject() {
				// 
				this.$refs.updateRef.validate(async (valid) => {
					if (!valid) return
					const response = await this.$request.put('/projects/' + this.editProject.id + '/', this
						.editProject)
					if (response.status === 200) {
						this.$message({
							message: '',
							type: 'success',
							duration: 1000
						})
						this.getProject()
						this.dialogEdit = false
					} else {
						this.$message({
							message: '',
							type: 'error',
							duration: 1000
						})
					}
				})
			},


			// 
			async proDelete(id) {
				console.log('id', id)
				// 
				const response = await this.$request.delete('/projects/' + id + '/')
				//  resetfuldelete204
				if (response.status === 204) {
					// 
					this.$message({
						message: '',
						type: 'success',
						duration: 1000
					});
					// 
					this.getProject()
				} else {
					this.$message({
						message: '',
						type: 'error',
						duration: 1000
					});
				}
			},
			// 
			async getProject() {
				// 
				const response = await this.$request.get('/projects/', {
					params: {
						page: this.page,
						size: this.size
					}
				})
				if (response.status !== 200) return this.$message.error('')
				// 
				this.projectList = response.data.results
				// 
				this.count = response.data.count
				console.log(response)
			},
			// 
			handleSizeChange(size) {
				this.size = size
				this.page = 1
				this.getProject()
			},
			// 
			handleCurrentChange(page) {
				this.page = page
				this.getProject()
			}
		},

		// vue
		mounted() {
			this.getProject()
		}

	}
</script>

<style>
.el-form-item__label {
  width: 150px ; /*  */
  white-space: nowrap; /*  */
  overflow: hidden; /*  */
  text-overflow: ellipsis; /*  */
  display: inline-block;
}
</style>
